import React, {Component, Fragment} from 'react';
import {Card, Row, Col, Table, Button} from 'antd';
import Filter from "./Filter";
import {postAction} from "../../../axios";
import ResultModal from './ResultModal';
import {setLocal} from "../../../utils";
import {hostUrl} from "../../../env";
import CenterModal from './CenterModal';


export default class YuJing extends Component {

    constructor(props){
        super(props);
        this.state = {
            dataSource:[],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            filterValue: {},
            studentInfo: {},
            careModalVisible: false,
            cancelCareModalVisible: false,
            id:"",
            center:false,
            centerModalVisible: false,
        }
        this.getFilterValueAndName = this.getFilterValueAndName.bind(this)
        this.getData = this.getData.bind(this);
    }

    // 列表数据请求
    getData () {
        const {filterValue: {curGrade, curCollege, curMajor, curNear, curScreening, curLevel, studentNo, curDeal}, pagination: {current, pageSize}} = this.state;
        postAction(
            "/bigdata/mentality/getPageData",
            {
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                time: curNear,
                finallyDealStatus: curScreening,
                alarmLevel: curLevel,
                studentNo: studentNo,
                dealType: curDeal,
                pageInfo: {
                    pageNum: current,
                    pageSize: pageSize
                },
            }
        ).then((res) => {
            if (res.obj.list && res.obj.list.length > 0) {
                let newData = [];
                res.obj.list.forEach(
                    (item, index) => {
                        newData.push({
                            key: item.id,
                            sortIndex: index + 1,
                            studentName: item.name,
                            studentId: item.studentId,
                            studentNo: item.studentNo,
                            gender: item.gender,
                            collegeName: item.collegeName,
                            majorName: item.majorName,
                            level: item.alarmLevelText,
                            update: item.updateDate,
                            schoolWarning: item.collegeDealStatusText,
                            schoolTime: item.collegeDealDate,
                            centerWarning: item.mentalityDealStatusText,
                            centerTime: item.mentalityDealDate,
                            detail: "详情",
                            collegeDealResult: item.collegeDealResult,
                            mentalityDealResult: item.mentalityDealResult
                        })
                    }
                )
                this.setState({
                    dataSource: newData,
                    pagination: {
                        current: res.obj.pageNum, // 当前页数，
                        pageSize: res.obj.pageSize, // 每页条数
                        total: res.obj.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    },
                })
            } else {
                this.setState({
                    dataSource: [],
                    pagination: {
                        current: 1, // 当前页数，
                        pageSize: 10, // 每页条数
                        total: 0, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    },
                })
            }
        })
    }

    // 表格分页
    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    }

    // 筛选条件
    getFilterValueAndName(obj) {
        this.setState(
            {
                filterValue: obj
            },
            () => {
                this.getData()
            }
        )
    }

    //打开未处理的弹窗  学校预警处理
    handleCareMocalOpen = (value) => {
        this.setState({
            careModalVisible: true,
            studentInfo: value,
        })
    }

    //打开未处理的弹窗  学校预警处理
    handleCenterMocalOpen = (value) => {
        this.setState({
            centerModalVisible: true,
            studentInfo: value,
        })
    }

    // 保存 未处理的弹窗 中心预警处理
    hideCenterModal = (obj) => {
        if (obj === undefined) {
            this.setState({
                centerModalVisible: false
            });
            return;
        }
        let {dealResult, dealRemark} = obj;
        let params = {
            "id": this.state.studentInfo.key,
            "mentalityDealResult": dealResult,
            "mentalityDealRemark": dealRemark,
            "dealType": 1
        };

        postAction(
            "/bigdata/mentality/update",
            params
        ).then((res) => {
            if (res.success) {
                this.setState({
                        centerModalVisible: false
                    },
                    () => {
                        this.getData();
                    }
                );
            }
        });

    }

    //保存 未处理的弹窗   学校预警处理
    hidecareModal = (obj) => {
        if (obj === undefined) {
            this.setState({
                careModalVisible: false
            });
            return;
        }
        let {dealResult, dealRemark} = obj;
        let params = {
            "id": this.state.studentInfo.key,
            "collegeDealResult": dealResult,
            "collegeDealRemark": dealRemark,
            "dealType": 0
        };

        postAction(
            "/bigdata/mentality/update",
            params
        ).then((res) => {
            if (res.success) {
                this.setState({
                        careModalVisible: false
                    },
                    () => {
                        this.getData();
                    }
                );
            }
        });

    }

    // componentDidMount() {
    //     this.getData()
    // }

    // 跳转
    gotoDetail = (obj) => { //跳转详情页面
        let data = {
            ...obj,
            backUrl: '/mainframe/XinLiJianKang/YuJing/index'
        };
        // 存储点击的列表信息到localstorage
        setLocal('xljk', JSON.stringify(data))
        this.props.history.push('/mainframe/XinLiJianKang/YuJing/detail/' + obj.key + "/" + obj.studentNo)
    };

    // 导出
    handleExport=()=>{
        const {filterValue: {curGrade, curCollege, curMajor, curNear, curScreening, curLevel, studentNo}} = this.state;
        window.location.href=`${hostUrl}/bigdata/mentality/exportStudentAlarm?collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&time=${curNear}&finallyDealStatus=${curScreening}&alarmLevel=${curLevel}&studentNo=${studentNo}`
    }


    render () {

        const columns = [
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width: "4%"
            },
            {
                title: '姓名',
                dataIndex: 'studentName',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '学号/教工号',
                dataIndex: 'studentId',
                width:"8%",
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '一卡通号',
                dataIndex: 'studentNo',
                width:"8%",
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '性别',
                dataIndex: 'gender',
                width:"4%",
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '院系',
                dataIndex: 'collegeName',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '专业',
                dataIndex: 'majorName',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '预警级别',
                dataIndex: 'level',
                width:"7%",
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '更新时间',
                dataIndex: 'update',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '学院预警处理',
                dataIndex: 'schoolWarning',
                width:"6%",
                render: (text, record) => {
                    return (
                        text === "未处理" ?
                            <span onClick={() => {
                                this.handleCareMocalOpen(record)
                            }} style={{cursor: "pointer", color: "#f04864"}}>
						未处理
					</span>
                            :
                            text === "已处理" ?
                                <span style={{color: "rgba(0,0,0,0.65)"}}>
						已处理
					</span> : null

                    )
                }
            },
            {
                title: '学院最近处理时间',
                dataIndex: 'schoolTime',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '中心预警处理',
                dataIndex: 'centerWarning',
                width:"6%",
                render: (text, record) => {
                    if(record.collegeDealResult === "转心理健康中心处理") {
                        return (
                            text === "未处理" ?
                                <span onClick={() => {
                                    this.handleCenterMocalOpen(record)
                                }} style={{cursor: "pointer", color: "#f04864"}}>未处理</span>
                                :
                                <span style={{color: "rgba(0,0,0,0.65)"}}>已处理</span>
                        )
                    } else {
                        return(
                            <span>——</span>
                        )
                    }
                }
            },
            {
                title: '中心最近处理时间',
                dataIndex: 'centerTime',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '详情',
                dataIndex: 'detail',
                width:"6%",
                render: (text, record) => {
                    return (
                        <span title={text} onClick={() => {
                            this.gotoDetail(record)
                        }} style={{
                            display: 'block',
                            width: '100%',
                            height: '100%',
                            cursor: 'pointer',
                            color: '#1890ff'
                        }}>{text}</span>
                    )
                }
            }
        ];

        return (
            <Fragment>
                <Row style={{margin: '-20px'}}>
                    <Col span={24} style={{backgroundColor:"rgba(255, 255, 255, 0.6)",padding:"20px 30px"}}>
                        <Filter getFilterValueAndName={this.getFilterValueAndName}/>
                    </Col>
                </Row>
                <Row style={{marginTop:"40px"}}>
                    <Card
                        headStyle={{border: 'none', fontSize: '16px'}}
                        bodyStyle={{paddingTop:0}}
                        extra={
                            <Button type='primary' onClick={this.handleExport}>导出</Button>
                        }
                    >
                        <Table
                            dataSource={this.state.dataSource}
                            pagination={this.state.pagination}
                            columns={columns}
                            bordered={true}
                            size="middle"
                            onChange={this.handleTableChange.bind(this)}
                        />
                    </Card>
                    <ResultModal
                        visible={this.state.careModalVisible}
                        handleOk={(obj) => {
                            this.hidecareModal(obj)
                        }}
                        handleCancel={this.hidecareModal}
                    />

                    <CenterModal
                        visible={this.state.centerModalVisible}
                        handleOk={(obj) => {
                            this.hideCenterModal(obj)
                        }}
                        handleCancel={this.hideCenterModal}
                    />

                </Row>
            </Fragment>
        )
    }
}


