import React, {Component, Fragment} from 'react';
import {Row, Col, Select, Button, Input} from 'antd';
import {postAction} from "../../../axios";


const { Option, OptGroup } = Select;

// const curYear = new Date().getFullYear();
// const curMonth = new Date().getMonth()+1;
// let yearList = [];
// for (let i = 0; i < 6; i++) {
//     yearList.push({
//         name: curYear - i + "-" + ((curYear - i) + 1),
//         value: curYear - i
//     })
// }

export default class Filter extends Component {

    constructor(props){
        super(props);
        this.state = {
            //学院
            college: '',
            collegeLists: [],

            //专业
            profession: '',
            professionList: [],

            //年级
            curGrade: '',
            gradeLists:[],

            //时间
            curNear:2018,  //最近
            curNearName:'',
            nearLists:[
                {name:'最近一月', value:1},
                {name:'最近一周', value:2}
            ],
            curYear: 2018,
            curYearName: "2018-2019",
            yearList:[
                {name: "2018-2019", value: 2018 },
				{name: "2017-2018", value: 2017 },
				{name: "2016-2017", value: 2016 },
            ],

            //预警筛选
            curScreening:'',
            screeningLists:[
                {name: '全部', value: ''},
                {name: '已处理', value: 1},
                {name: '未处理', value: 0}
            ],

            //预警级别
            curLevel:'',
            levelLists:[
                {name:'全部预警', value: ''},
                {name:'红色预警', value:1 },
                {name:'橙色预警', value:2 },
                {name:'黄色预警', value:3 },
                {name:'蓝色预警', value:4 }
            ],

            //预警处理
            curDeal: 1,
            dealLists:[
                {name: '全部', value: ''},
                {name: '需要心理健康中心处理', value: 1},
                {name: '需要学院处理', value: 0}
            ],

            //学号or姓名
            studentNo:''
        }
    }

    // 获取学院list
    getColledgeLists() {
        postAction("/bigdata/school/listCollege").then((res) => {
            if (res.success) {
                this.setState({
                    college: '',
                    collegeLists: res.obj
                })
            }
        })
    }

    // 获取专业list
    getMajorLists() {
        postAction(
            "/bigdata/school/listMajorByCollege",
            {
                collegeCode: this.state.college
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        profession: "",
                        professionList: res.obj
                    })
                }
            }
        )
    }

    // 获取年级list
    getGradeLists() {
        postAction(
            "/bigdata/school/getGradeList",
            {
                year: this.state.curYear,
                collegeCode: this.state.college,
                majorCode: this.state.profession
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        curGrade: "",
                        gradeLists: res.obj
                    })
                }
            }
        )
    }

    //筛选条件
    changeFilter = (num, value, option) => {
        if (num === 1) { //时间
            this.setState({
                curNear: value,
                curYear: value,
                curYearName: option.props.children,
            })
        } else if (num === 2) { //学院
            this.setState(
                {
                    college: value,
                    curCollegeName: option.props.children,
                },
                () => {
                    this.getMajorLists()
                    this.getGradeLists()
                }
            )
        } else if (num === 3) { //专业
            this.setState(
                {
                    profession: value,
                    professionName: option.props.children,
                },
                () => {
                    this.getGradeLists()
                }
            )
        } else if (num === 4) { //年级
            this.setState(
                {
                    curGrade: value,
                    curYear: value,
                    curGradeName: option.props.children,
                }
            )
        } else if (num === 5) {
            this.setState({
                curScreening: value,
                screeningName: option.props.children,
            })
        } else if (num === 6) {
            this.setState({
                curLevel: value,
                levelName: option.props.children,
            })
        } else if (num === 7) {
            this.setState({
                curDeal: value,
                dealName: option.props.children,
            })
        }
    }

    studyNum(event) {
        this.setState({ studentNo: event.target.value });
    }

    componentDidMount() {
        this.getColledgeLists()
        this.getMajorLists()
        this.getGradeLists()
        this.searchMore()
    }

    //点击搜索
    searchMore() {
        const { college, profession, curGrade, curYear, curScreening, curLevel, studentNo, curDeal } = this.state;
        let obj = {
            curCollege: college,
            curMajor: profession,
            curGrade: curGrade,
            curNear: curYear,
            curScreening: curScreening,
            curLevel: curLevel,
            studentNo: studentNo,
            curDeal: curDeal,
        };
        this.props.getFilterValueAndName(obj);
    }

    render () {

        const { college, collegeLists, profession, professionList, curGrade, gradeLists, curNear, nearLists, curScreening, screeningLists, curLevel, levelLists, studentNo, dealLists, curDeal} = this.state;

        return (

            <Fragment>
                <Row>
                    <Col span={24}>
                        {/*第一排*/}
                        <Row style={{marginBottom:"10px"}}>
                            {/*时间*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>时间：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={curNear}
                                            style={{ width: "90%" }}
                                            onChange={this.changeFilter.bind(this, 1)}
                                        >
                                            <OptGroup label="最近">
                                                {
                                                    nearLists.map((item, index) =>
                                                        (
                                                            <Option key={index} value={item.value}>{item.name}</Option>
                                                        ))
                                                }
                                            </OptGroup>
                                            <OptGroup label="学年">
                                                {
                                                    this.state.yearList.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                                }
                                            </OptGroup>
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                            {/*学院*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>学院：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={college}
                                            style={{width:"90%"}}
                                            placeholder="请选择学院"
                                            onChange={this.changeFilter.bind(this, 2)}
                                        >
                                            {
                                                collegeLists.map((item, index) => (
                                                    <Option
                                                        key={index}
                                                        title={item.collegeName}
                                                        value={item.collegeCode}
                                                    >
                                                        {item.collegeName}
                                                    </Option>
                                                ))
                                            }
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                            {/*专业*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>专业：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={profession}
                                            style={{width:"90%"}}
                                            placeholder="请选择专业"
                                            onChange={this.changeFilter.bind(this, 3)}
                                        >
                                            {
                                                professionList.map((item, index) => (
                                                    <Option
                                                        key={index}
                                                        title={item.majorName}
                                                        value={item.majorCode}
                                                    >
                                                        {item.majorName}
                                                    </Option>
                                                ))
                                            }
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                        </Row>

                        {/*第二排*/}
                        <Row>
                            {/*年级*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>年级：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={curGrade}
                                            style={{width:"90%"}}
                                            placeholder="请选择年级"
                                            onChange={this.changeFilter.bind(this, 4)}
                                        >
                                            {
                                                gradeLists.map((item, index) =>
                                                    (
                                                        <Option key={index} value={item.name}>{item.value}</Option>
                                                    ))
                                            }
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                            {/*预警筛选*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>预警筛选：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={curScreening}
                                            style={{width:"90%"}}
                                            placeholder="请选择预警"
                                            onChange={this.changeFilter.bind(this, 5)}
                                        >
                                            {
                                                screeningLists.map((item, index) => (
                                                    <Option key={index} title={item.name} value={item.value}>{item.name}</Option>
                                                ))
                                            }
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                            {/*预警级别*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>预警级别：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={curLevel}
                                            style={{width:"90%"}}
                                            placeholder="请选择预警级别"
                                            onChange={this.changeFilter.bind(this, 6)}
                                        >
                                            {
                                                levelLists.map((item, index) => (
                                                    <Option key={index} title={item.name} value={item.value}>{item.name}</Option>
                                                ))
                                            }
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                        </Row>

                        {/*第三排*/}
                        <Row style={{marginTop:"10px"}}>
                            {/*预警处理*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span>预警处理：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Select
                                            value={curDeal}
                                            style={{width:"90%"}}
                                            onChange={this.changeFilter.bind(this, 7)}
                                        >
                                            {
                                                dealLists.map((item, index) => (
                                                    <Option key={index} title={item.name} value={item.value}>{item.name}</Option>
                                                ))
                                            }
                                        </Select>
                                    </Col>
                                </Row>
                            </Col>

                            {/*姓名 / 学号*/}
                            <Col span={8}>
                                <Row>
                                    <Col span={6}>
                                        <span style={{lineHeight:"32px"}}>姓名/学号：</span>
                                    </Col>
                                    <Col span={18}>
                                        <Input
                                            placeholder="请输入学生姓名/学号"
                                            value={studentNo}
                                            onChange={this.studyNum.bind(this)}
                                            style={{ width:"90%" }}
                                        />
                                    </Col>
                                </Row>
                            </Col>

                            {/*搜索*/}
                            <Col span={6}>
                                <Button type="primary" icon="search" onClick={() => this.searchMore()}>搜  索 </Button>
                            </Col>
                        </Row>

                    </Col>
                </Row>
            </Fragment>
        )
    }
}


