import React, {Component} from 'react';
import {Modal, Form, Radio, Input, Button} from 'antd';


const FormItem = Form.Item
const RadioGroup = Radio.Group;
const {TextArea} = Input;

class Care extends Component {

    constructor(props) {
        super(props);
        this.state = {
            textareaVisible: false,
            data: [
                {
                    "code": "1",
                    "name": "转心理健康中心处理"
                },{
                    "code": "2",
                    "name": "需要学院继续关注"
                },{
                    "code": "3",
                    "name": "其他"
                },
            ],
            disabled: false,
            dealRemark: '',
            dealResult: ""
        };
        // this.getData = this.getData.bind(this);
    }

    hideModal = () => {
        // 重置form表单
        this.props.form.resetFields()
        this.props.handleCancel()
    };

    handleOk = () => {
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                // console.log('Received values of form: ', values);
                // this.props.form.resetFields()
                // values可以取到对应的属性值
                // 调接口发送请求
                this.props.handleOk({
                    dealRemark: this.state.dealRemark,
                    dealResult: this.state.dealResult
                })
                    // 重置form表单
                    this.props.form.resetFields()
                    this.setState({
                        dealRemark: '',
                        dealResult: ""
                    })

            }
        });

    };

    handlerBlurTips = (e) => {
        let value = e.target.value;
        if (value.length > 15) {
            this.setState({
                disabled: true
            });
        } else {
            this.setState({
                dealRemark: value,
                disabled: false
            });
        }
    };

    // 显示底部的textarea
    handleChange = (e) => {
        let choose = e.target.value;
        this.setState({
            textareaVisible: false,
            dealResult: choose
        })
    }

    render() {
        const {getFieldDecorator} = this.props.form
        const formItemLayout = {
            labelCol: {
                span: 4
            },
            wrapperCol: {
                span: 20
            },
        };
        return (
            <React.Fragment>
                <Modal
                    title="学院预警处理"
                    visible={this.props.visible}
                    onOk={this.handleOk}
                    onCancel={this.hideModal}
                    okText="确认"
                    cancelText="取消"
                    footer={null}
                >
                    <div style={{padding: '0 30px'}}>
                        <Form>
                            <FormItem label='选择原因' {...formItemLayout}>
                                {getFieldDecorator('reason',
                                    {
                                        initialValue: "0"
                                    }
                                )(
                                    <RadioGroup onChange={(e) => {
                                        this.handleChange(e)
                                    }}>
                                        {
                                            this.state.data.map((item, index) => (
                                                <Radio
                                                    key={index}
                                                    title={item.name}
                                                    value={item.name}
                                                >
                                                    {item.name}
                                                </Radio>
                                            ))
                                        }
                                    </RadioGroup>
                                )}
                            </FormItem>
                            {
                                <FormItem label='备注' {...formItemLayout}>
                                    {getFieldDecorator('mark',
                                        {
                                            rules: [{
                                                max: 15,
                                                message: '请输入不超过15个字',
                                            }]
                                        }
                                    )(
                                        <TextArea rows={4} placeholder='请输入原因（不超过15个字）'
                                                  onChange={(e) => {
                                                      this.handlerBlurTips(e)
                                                  }}/>
                                    )}
                                </FormItem>
                            }

                            <FormItem style={{textAlign: 'right'}}>
                                <Button onClick={this.handleOk} type='primary'
                                        disabled={this.state.disabled}>确认</Button>
                                <Button onClick={this.hideModal} style={{marginLeft: '10px'}}>取消</Button>
                            </FormItem>
                        </Form>
                    </div>


                </Modal>

            </React.Fragment>
        );
    }
}

Care = Form.create()(Care)
export default Care;


